
.nav {
    grid-area: n;
    background-color: #79589f;
    padding: 20px;
    box-shadow: 2px 0px 2px rgba(0,0,0,0.2);
    z-index: 50;
  }
  
.item a { 
  color:white;
  text-decoration: none;

}

.item a.activeLink 
{ 
  color:gold;
}

.item {
    position: relative;
    /*border-radius: 0 0 0 10px;*/
    background-color: #bebed8;
    border: 1px solid #574796;
    border-bottom: 4px solid #3f3179;
    color: #FFF;
    margin: 0 auto;
    padding: 5px;
    width: 100%;
}

.ugol {
    position: relative;
    text-indent: 0px;
    margin-left: -15px; /* 30 padding conteiner + 1 border conteiner + 10 border before */
    margin-right: -15px;
    padding: 5px 25px;
    background: #574796;
    color: #2f3251;
}

.ugol:after{
    position: absolute;
    left: 0;
    bottom: -10px;
    content:'';
    border-top: 10px solid #172755;
    border-left: 10px solid transparent;
}

.ugol:before{
    position: absolute;
    right: 0;
    bottom: -10px;
    content:'';
    border-top: 10px solid #172755;
    border-right: 10px solid transparent;
}
